import React, { useEffect, useRef, useState} from 'react';
import style from "./Login.module.css";
import {NavLink, useNavigate} from "react-router-dom";
import AuthHeader from "../Header/AuthHeader";
import {useFormik} from "formik";
import { useAppDispatch } from '../../../../CustomHooks/UseAppDispatch';
import { getUserProfileTC } from '../../../../BLL/AuthReducer';
import { useSelector } from 'react-redux';
import { AppRootStateType } from '../../../../BLL/Store';
import { GetUserProfileResType } from '../../../../DAL/Api';
import { setNotifyMessageOkAC } from '../../../../BLL/AppReducer';



/**
 *    
 */

const Login = () => {
    const profileData = useSelector<AppRootStateType, GetUserProfileResType | null>(state => state.AuthReducer.profileData ); 
    /**
     *         ,     
     */
    const dispatch = useAppDispatch()
    /**
     *        ko  
     */
    const [showErrors, setShowErrors] = useState(false);
    const handleClickShowErrors = () => {
        setShowErrors(true);
      };
    /**
     *   
     */
    const navigate = useNavigate()
  
    /**
     *       
     */
    const [showPassword, setShowPassword] = useState<boolean>(false);
    /**
     *      
     */
    const handleClickShowPassword = () => {
        setShowPassword(!showPassword);
    };
    const handleMouseDownPassword = (event: React.MouseEvent<HTMLButtonElement>) => {
        event.preventDefault();
    };
    /**
     *      
     */
    const isValidEmail = (email: string) => {
        const emailRegex = /^[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,}$/;
        return emailRegex.test(email);
    };
    /**
     *   ,  , 
     */
    const formik = useFormik({
        initialValues: {
            email: '',
            password: ''
        },
        onSubmit: values => {
                 dispatch(getUserProfileTC(values))        
                 formik.resetForm()
        },
        validate: (values) => {
            const errors: any = {};
            
              if (!values.email) {
                  errors.email = ",    e-mail";
              } else if (!isValidEmail(values.email)) {
                  errors.email = ",   e-mail";
              }

              if (!values.password) {
                  errors.password = ",  ";
              }
            console.log(errors)
            return errors;
        }
    });
    /**
     *            ,     e profileData    null,        
     * 
     */
    useEffect(()=> {
        if(profileData !== null){
            dispatch(setNotifyMessageOkAC("  "))
           const timer = setTimeout(()=> {
                navigate("/cabinet");
            },1500)
            return () => clearTimeout(timer)
        } 
    }, [profileData])
    
    return (
        <div className={style.container}>
            <AuthHeader/>
            <div className={style.loginContainer}>
                <div className={style.loginFormContainer}>
                    <h3></h3>
                    <form onSubmit={formik.handleSubmit} className={style.form}>
                        <input
                            type="email"
                            placeholder={" e-mail"}
                            {...formik.getFieldProps("email")}
                            className={
                               formik.errors.email
                                    ? style.errorInput
                                    : style.input
                            }
                        />
                        {formik.errors.email && showErrors && (
                            <div className={style.errorMessage}>
                                {formik.errors.email}
                            </div>
                        )}
                            <div className={formik.touched.password && formik.errors.password
                                ? style.errorInput
                                : style.input}>
                                <input
                                    style={{width : "95%"}}
                                    type={showPassword ? "text" : "password"}
                                    placeholder={" "}
                                    {...formik.getFieldProps("password")}
                                />
                                <i className='bx bx-show' onClick={handleClickShowPassword} onMouseDown={handleMouseDownPassword}></i>
                                {/*<span onClick={handleClickShowPassword}><img src="../../../../assets/icons/showPassSvg.svg" /></span>*/}
                            </div>

                        {formik.errors.password && showErrors && (
                            <div className={style.errorMessage}>
                                {formik.errors.password}
                            </div>
                        )}
                        <button type="submit" className={style.btn} onClick={handleClickShowErrors}></button>
                    </form>
                    <div className={style.footer}>
                        <NavLink to={"/register"} className={style.registerLink}> ?
                            </NavLink>
                        <NavLink to={"/user/:id/recovery/:token"} className={style.recoverLink}> ?</NavLink>
                    </div>
                </div>
            </div>
        </div>
    )
};

export default Login;